import { Component, OnInit,EventEmitter,Output,Inject } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { RestService } from '../RestService/restservice';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import * as global from '../../global';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {MatTableDataSource} from '@angular/material';
import { FormArray } from '@angular/forms';
import { AbstractControl } from '@angular/forms/src/model';
@Component({
  selector: 'app-userssettings',
  templateUrl: './userssettings.component.html',
  styleUrls: ['./userssettings.component.css']
})
export class UserssettingsComponent implements OnInit {
  dataSource: MatTableDataSource<{}>;
  displayedColumns = ['name','userrole', 'email','mobile_no','assigned_to','actions'];
  
  viewusers:any;
  userid="19";
  deleteids:any;
  usertype="Employer";
  type="1";
  cmpny_id="13";
  edit:any;
  @Output() jobEmitter= new EventEmitter<number>();
  constructor(public dialog: MatDialog,private router:Router,private activatedRoute: ActivatedRoute, private restService:RestService) { }
  
    ngOnInit() {
  this.viewuser();
 
    }
    viewuser(){
      var body =
      {
        userid : "19",
        input_role:"1"
      }
      this.restService.postdetails(global.baseUrl+'view_user',body).subscribe((response:any)=>{
        if(response.message)
        {
          this.viewusers=response.message;
          this.dataSource = new MatTableDataSource(this.viewusers);
         //console.log(this.viewusers)
     
        }
        else
        {
           console.log(response.message);
        }
      }); 
    }

    openDialog(): void {
      let dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
        width: '800px',
        data:{}
      });
  
      dialogRef.afterClosed().subscribe(result => {
        //console.log(result)
        if(result==undefined){
          this.dataSource = new MatTableDataSource(this.viewusers);
        }
        else{
        this.viewusers.push(result);
        this.dataSource = new MatTableDataSource(this.viewusers);
      // this.dataSource.data.push(result);
     // console.log( this.viewusers);
      }
      });
    }
    onView(ids:number)
    {
      //console.log(ids)
      this.jobEmitter.emit(ids);
      this.router.navigate(['viewuserdetails/'+ids]);
    }
    onEdit(ids:number)
    {
      this.jobEmitter.emit(ids);
      this.router.navigate(['edituserdetails/'+ids]);
    }
    onDelete(ids:number){
      var retVal = confirm("Are you sure want to delete user?");
      if( retVal == true ){
         this.deleteids=ids;
         let body:any;
         body=
         {
           userId:this.deleteids
         };
         this.restService.postdetails(global.baseUrl+'deleteuser',body).subscribe((response:any)=>{
           if(response.message)
           {
             
             //console.log(response.message);
             this.viewuser();           
           }
           else{

           }
      });
    }
      else{
         return false;
      }
   }

  }
    
  @Component({
    selector: 'dialog-overview-example-dialog',
    templateUrl: 'dialog-overview-example-dialog.html',
    styleUrls: ['./dailog.css']
  })
  export class DialogOverviewExampleDialog implements OnInit {
    mobnumPattern = /^(?:(?:\+|0{0,2})91(\s*[\-]\s*)?|[0]?)?[789]\d{9}$/; 
    nameRegex=/^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/; 
    emailRegex=/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  
      
    listPrivilege=[{
      id: 1,
      name: "Job Posting"
    },
    {
      id: 2,
      name: "Profile View"
    },
    {
      id: 3,
      name: "Resume"
    },
    {
      id: 4,
      name: "Active"
    },
    {
      id: 5,
      name: "Inactive"
    },
    {
      id: 6,
      name: "Closed"
    },
  ]

    rolesmeta:any;
    assignmeta:any;
    isLinear = false;
    userid="19";
    usertype="Employer";
    userrole:any;
    type="1";
    cmpny_id="13";
    errorMessage:any;
    userForm: FormGroup;
    privelgeFormGroup: FormGroup;
    constructor(private router:Router, private restService:RestService,
      public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
      @Inject(MAT_DIALOG_DATA) public data: any) { 
        data={"ff":"ff"};
        this.restService.getdetails(global.baseUrl+'rolesmeta').subscribe((response:any)=>{
          if(response.message)
          {
            this.rolesmeta=response.message;;
           
       
          }
          else
          {
            console.log(response.message);
          }
        }); 
       
       
        this.privelgeFormGroup = new FormGroup({
          privilege: new FormArray([
           new FormControl(false),
           new FormControl(false),
           new FormControl(false),
           new FormControl(false),
           new FormControl(false),
           new FormControl(false)])
      
        });
        //console.log(this.privelgeFormGroup)
      }
  ngOnInit(){
    this.userForm = new FormGroup({
      'username': new FormControl('',[Validators.required,Validators.pattern(this.nameRegex)]),
      'email': new FormControl('',[Validators.required, Validators.email,this.getEmailMessage.bind(this)]),
      'password': new FormControl('',[Validators.required,Validators.pattern(global.passwordRegex)]),
      'mobile': new FormControl('',[Validators.required,Validators.pattern(this.mobnumPattern)]),
      // designation: new FormControl('',[Validators.required]),
      'userrole': new FormControl('', [Validators.required]),
      'assigned': new FormControl('',[Validators.required])
    });
  }
    onNoClick(): void {
    
    }
  
    onCreate(){
      let body:any;
      body=
      {
        name:this.userForm.get('username').value,
        email:this.userForm.get('email').value,
        mobile_no:this.userForm.get('mobile').value,
        password:this.userForm.get('password').value,
        // designation:this.userForm.get('designation').value,
        adminid:this.userid,
        cmpny_id:this.cmpny_id,
        usertype:this.usertype,
        assigned_to:this.userForm.get('assigned').value,
        userrole:this.userForm.get('userrole').value
      }
      this.restService.postdetails(global.baseUrl+'createuser',body).subscribe((response:any)=>
      {
        if(response.status)
        {
        //console.log(response);
        if(response.message.userid==null){
          alert(response.message.Result);
          this.dialogRef.close();
        }
        else{
        var userId = response.message.userid;
        body.ids= userId;
        let rolesIndex = this.rolesmeta.findIndex(roles => {return roles.Role_Id== body.userrole ? true : false;});
        let assignedIndex = this.assignmeta.findIndex(meta => {return meta.id== body.assigned_to ? true : false});

        this.data={
          assigned_to:this.assignmeta[assignedIndex].contact_name,
          // designation : "",
          email: body.email,
          ids: userId,
          mobile_no: body.mobile_no,
          name: body.name,
          userrole: this.rolesmeta[rolesIndex].Role_Name
        };
        //console.log(this.data)
        this.dialogRef.close(this.data);
        let userPrivileges = Object.assign({}, {
          priveleges: this.privelgeFormGroup.controls.privilege.value.map((selected, i)=>{
    if(selected)
    {
    return {
              "feature":this.listPrivilege[i].id,
              "feat_status": "Active",
              userid : userId,
              org_subs_id:"1",
            }
    }
    else
    {
      return {
              "feature":this.listPrivilege[i].id,
              "feat_status": "Inactive",
              userid : userId,
              org_subs_id:"1",
            }
    }     
          }) 
        });
       
        //console.log("hi")
        this.restService.postdetails(global.baseUrl+'addeditPrivilege',userPrivileges).subscribe((response1:any)=>
        {
        
          if(response1.status){
           // this.router.navigate(['/usersettings']);
           //console.log(this.data)
            this.dialogRef.close(this.data);
            
            alert("created Sucessfully");
           
         
         
      }
      
      else
      {
        //console.log(response1);
        this.errorMessage=response1.message;
        alert("errorMessage");
      }
      });
    }
  }
  })
}
roleChange(){

    let assign:any;
    assign={
      cmpny_id:this.cmpny_id,
      user_type:this.userrole,
      
      
    };
  
    this.restService.postdetails(global.baseUrl+'assignedmeta',assign).subscribe((response:any)=>{
      if(response.message)
      {
        this.assignmeta=response.message;;
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 

}
getEmailMessage(control: FormControl){
  if(control)
  {
    if(control.value.includes("@gmail.com")||control.value.includes("@yahoo.com")||control.value.includes("@orkut.com")||control.value.includes("@rediffmail.com")||control.value.includes("@hotmail.com")||control.value.includes("@outlook.com")) {
      return {"isInvalid": true};
     }
       else
      return null;
  }
  else return null;
   
  }
 
}
        





