import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as global from '../../../global';
import {RestService} from '../../RestService/restservice';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-viewuser',
  templateUrl: './viewuser.component.html',
  styleUrls: ['./viewuser.component.css']
})
export class ViewuserComponent implements OnInit {
  editForm:FormGroup;
  edit:any;
  errorMessage:any;
  assigned=227;
  rolesmeta:any;
  index:any;
  viewusers:any;
  buttonWasClicked: boolean = false;
  constructor(private router:Router, private restService:RestService, private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.restService.getdetails(global.baseUrl+'rolesmeta').subscribe((response:any)=>{
      if(response.message)
      {
        this.rolesmeta=response.message;;
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 
    //console.log(this.index=this.activatedRoute.snapshot.params['id']);
    // this.edit=this.restService.viewuser[this.index];
   this.restService.getdetails(global.baseUrl+'view_user').subscribe((response:any)=>{
     if(response.message)
     {
       this.viewusers=response.message;;
       this.edit=this.viewusers[this.index]
      //console.log(this.edit);
     }
     else
     {
        //console.log(response.message);
     }
   }); 
   this.editForm= new FormGroup({
      'designation': new FormControl(null,),
      'name': new FormControl(null, Validators.required),
      'role': new FormControl(null, Validators.required),
      'email': new FormControl(null, ),
      'assigned': new FormControl(null,),
      'mobile': new FormControl(null,Validators.required )
    });


 }
 onDelete(){
  let body:any;
  body=
  {
    userId:this.edit.ids
  };
  this.restService.postdetails(global.baseUrl+'deleteuser',body).subscribe((response:any)=>{
    if(response.message)
    {
      //console.log(response.message);
      alert("Deleted successfully");
      this.router.navigate(['/users/']);
    }
    else
    {
       //console.log(response.message);
    }
  }); 
 }
 setButtonClicked (clicked: boolean) {
  this.buttonWasClicked = clicked;
}
onSave(){

  let  body=
  {
    'userid':this.edit.ids,
    'name':this.editForm.get('name').value,
    'mobile_no':this.editForm.get('mobile').value,
    'assigned_to':this.assigned,
    'userrole':this.editForm.get('role').value
  };
  //console.log(body,this.editForm.get('name'));
   this.restService.updateDetails(global.baseUrl+'edit_user',body).subscribe((response:any)=>{
    if(response.message)
    {
      //console.log(response.message);
      alert("updated successfully");
      this.router.navigate(['/users/']);
    }
    else
    {
       //console.log(response.message);
    }
  });  
 }
}




