import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as global from '../../../global';
import {RestService} from '../../RestService/restservice';
import { Router, ActivatedRoute } from '@angular/router';
import { FormArray } from '@angular/forms';
import { forEach } from '@angular/router/src/utils/collection';
@Component({
  selector: 'app-edituserdetails',
  templateUrl: './edituserdetails.component.html',
  styleUrls: ['./edituserdetails.component.css']
})
export class EdituserdetailsComponent implements OnInit {
  mobnumPattern = /^(?:(?:\+|0{0,2})91(\s*[\-]\s*)?|[0]?)?[789]\d{9}$/; 
  nameRegex=/^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/; 
  assignmeta: any;
  index:any;
  pri:any;
  role:any;
  editForm:FormGroup;
  privelgeFormGroup: FormGroup;
  viewusers:any
  edit:any;
  assigned="227";
  rolesmeta:any;
  listPrivilege=[{
    id: 1,
    name: "Job Posting"
  },
  {
    id: 2,
    name: "Profile View"
  },
  {
    id: 3,
    name: "Resume"
  },
  {
    id: 4,
    name: "Active"
  },
  {
    id: 5,
    name: "Inactive"
  },
  {
    id: 6,
    name: "Closed"
  },
]
  constructor(private router:Router, private restService:RestService, private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    console.log(this.index=this.activatedRoute.snapshot.params['id']);
   
    this.editForm= new FormGroup({

      'name': new FormControl('',[Validators.required,Validators.pattern(this.nameRegex)]),
      'role': new FormControl('',[Validators.required]),
      'email': new FormControl(null),
      'assigned': new FormControl('',[Validators.required]),
      'mobile': new FormControl('',[Validators.required,Validators.pattern(this.mobnumPattern)]),
    });
    this.privelgeFormGroup = new FormGroup({
      privilege: new FormArray([
       new FormControl(),
       new FormControl(),
       new FormControl(),
       new FormControl(),
       new FormControl(),
       new FormControl()])
  
    });
    //console.log(this.privelgeFormGroup)
  
    this.restService.getdetails(global.baseUrl+'rolesmeta').subscribe((response:any)=>{
      if(response.message)
      {
        this.rolesmeta=response.message;;
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 
    let assign:any;
    assign={
      user_type:this.role,
      cmpny_id:13
      
    };
  
    this.restService.postdetails(global.baseUrl+'assignedmeta',assign).subscribe((response:any)=>{
      if(response.message)
      {
        this.assignmeta=response.message;
        //console.log(this.assignmeta)
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 
   
    // this.edit=this.restService.viewuser[this.index];
    var body = {
      'userid': this.index
    }
   this.restService.postdetails(global.baseUrl+'viewsingleuser',body).subscribe((response:any)=>{
     if(response.message)
     {
       this.edit=response.message[0];

       
       this.editForm.controls['email'].setValue(this.edit.email);
       this.editForm.controls['name'].setValue(this.edit.name);
    
       this.editForm.controls['mobile'].setValue(this.edit.mobile_no);
      this.editForm.controls['assigned'].setValue(this.edit.assgnd_to);
      this.editForm.controls['role'].setValue(this.edit.userrole_id);

      let assign:any;
      assign={
        user_type:this.edit.userrole_id,
        cmpny_id:13
        
      };
    
      this.restService.postdetails(global.baseUrl+'assignedmeta',assign).subscribe((response:any)=>{
        if(response.message)
        {
          this.assignmeta=response.message;
          //console.log(this.assignmeta)
         
     
        }
        else
        {
           //console.log(response.message);
        }
      }); 
     
      //console.log(this.edit);
     }
     else
     {
        //console.log(response.message);
     }
   }); 
   var pribody = {
    'userid': this.index,
    'org_subs_id':1
  }
  //console.log(body);
  this.restService.postdetails(global.baseUrl+'viewuserpri',pribody).subscribe((response:any)=>{
    if(response.message)
    {
      this.pri=response.message;
     /*  for(let controls of formArray) {
        //console.log(controls)
        controls.setValue(true)
      } */
      this.pri.forEach((element, index) => {
       
        if(element.sts === "Active")
        {
         // //console.log( this.privelgeFormGroup.controls.privilege.get(index.toString()).setValue(true))
          this.privelgeFormGroup.controls.privilege.get(index.toString()).setValue(true)
        }
        else {
          this.privelgeFormGroup.controls.privilege.get(index.toString()).setValue(false)
        }
        //console.log(element)
      });
     //console.log( this.privelgeFormGroup.controls.privilege)

    
    }
    else
    {
       //console.log(response.message);
    }
    //console.log(this.privelgeFormGroup.controls.privilege);
  });
  }
  onSave(){
    
      let  body=
      {
        'userid':this.edit.id,
        'name':this.editForm.get('name').value,
        'mobile_no':this.editForm.get('mobile').value,
        'assigned_to':this.editForm.get('assigned').value,
        'userrole':this.editForm.get('role').value,
        'email':this.editForm.get('email').value,

      };
      //console.log(body);
       this.restService.postdetails(global.baseUrl+'edit_user',body).subscribe((response:any)=>{
        if(response.message)
        {
          //console.log(response.message);
          this.preeeevilegeee();
          this.router.navigate(['/usersettings']);
        }
        else
        {
           //console.log(response.message);
        }
      });  
     }
     preeeevilegeee(){
     let userPrivileges = Object.assign({}, {
      priveleges: this.privelgeFormGroup.controls.privilege.value.map((selected, i)=>{
if(selected)
{
return {
          "feature":this.listPrivilege[i].id,
          "feat_status": "Active",
          userid : this.edit.id,
          org_subs_id:"1",
        }
}
else
{
  return {
          "feature":this.listPrivilege[i].id,
          "feat_status": "Inactive",
          userid : this.edit.id,
          org_subs_id:"1",
        }
}     
      }) 
    });
    this.restService.postdetails(global.baseUrl+'addeditPrivilege',userPrivileges).subscribe((response1:any)=>
    {
    
      if(response1.status){
        alert("updated Sucessfully");
      }
      else{
        //console.log(response1.message);
      }
    });
  }
  cancel(){
    this.router.navigate(['/usersettings']);
  }
  roleChange(){
    let assign:any;
    assign={
      user_type:this.role,
      cmpny_id:13
      
    };
  
    this.restService.postdetails(global.baseUrl+'assignedmeta',assign).subscribe((response:any)=>{
      if(response.message)
      {
        this.assignmeta=response.message;
        //console.log(this.assignmeta)
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 
  }
}



