import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as global from '../../../../global';
import {RestService} from '../../../RestService/restservice';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-edituserprev',
  templateUrl: './edituserprev.component.html',
  styleUrls: ['./edituserprev.component.css']
})
export class EdituserprevComponent implements OnInit {
  index:any;
  jobpostings:any;
  viewusers:any;
  edit:any;
  userid:any;
  isEdit : boolean = false;
  constructor(private router:Router, private restService:RestService, private activatedRoute: ActivatedRoute) { 
    //console.log(this.index=this.activatedRoute.snapshot.params['id']);
    this.restService.getdetails(global.baseUrl+'view_user').subscribe((response:any)=>{
      if(response.message)
      {
        this.viewusers=response.message;;
        this.edit=this.viewusers[this.index];
        //console.log(this.userid=this.viewusers[this.index].ids);
        this.userprevilege();
      }
      else
      {
         //console.log(response.message);
      }
    }); 
  }

  ngOnInit() {


}
userprevilege(){
  let body:any;
body={
'userid':this.userid
};
 this.restService.postdetails(global.baseUrl+'viewuserpri',body).subscribe((response:any)=>{
   if(response.message)
  {

    this.jobpostings=response.message[0].Quantity;
    //console.log(this.jobpostings);
}
else
{
    //console.log(response.message);
  }
 }); 
 }
 setButtonClicked (clicked: boolean) {
  this.isEdit = clicked;
}
}



