import { Component, OnInit,EventEmitter,Output } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import * as global from '../../../global';
import { Router } from '@angular/router';
import {RestService} from '../../RestService/restservice';
@Component({
  selector: 'app-userprivileges',
  templateUrl: './userprivileges.component.html',
  styleUrls: ['./userprivileges.component.css']
})
export class UserprivilegesComponent implements OnInit {
  userid="19";
  assigned="227";
  usertype="Employer";
  expanded:boolean=false;
  cmpny_id="13";
  usersprivelege:any;
  selectedvalue:any;
  viewusers:any;
  isEdit : boolean = true;
  @Output() jobEmitter= new EventEmitter<number>();
  constructor(private router:Router, private restService:RestService) { }

  ngOnInit() {
    this.restService.getdetails(global.baseUrl+'view_user').subscribe((response:any)=>{
      if(response.message)
      {
        //console.log(response.message);
        this.viewusers=response.message;;
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 

   
  }
  panelOpenState: boolean = false;
  setButtonClicked () {
    this.isEdit = false;
  }
  viewprivelege(ids:number)
  {
    //console.log(ids);
      this.jobEmitter.emit(ids);
      this.router.navigate(['edituserprev/'+ids]);
  }
  // viewprivelege(index)
  // {
  //   this.selectedvalue= this.viewusers[index].ids
  //   console.log(this.selectedvalue);
  //   let body:any;
  //   body={
  // 'userid':this.selectedvalue
  //   };
  //   this.restService.postdetails(global.baseUrl+'viewuserpri',body).subscribe((response:any)=>{
  //     if(response.message)
  //     {
  //       console.log(response.message);
  //       this.usersprivelege=response.message;;
       
   
  //     }
  //     else
  //     {
  //        console.log(response.message);
  //     }
  //   }); 
  // }
  
}



