import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as global from '../../../global';
import {RestService} from '../../RestService/restservice';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-viewuserdetails',
  templateUrl: './viewuserdetails.component.html',
  styleUrls: ['./viewuserdetails.component.css']
})
export class ViewuserdetailsComponent implements OnInit {
  index:any;
  viewusers:any;
  edit:any;
  pri:any;
  listPrivilege=[{
    id: 1,
    name: "Job Posting"
  },
  {
    id: 2,
    name: "Profile View"
  },
  {
    id: 3,
    name: "Resume Download"
  },
  {
    id: 4,
    name: "Active Job"
  },
  {
    id: 5,
    name: "Inactive Job"
  },
  {
    id: 6,
    name: "Closed Job"
  },
]
  constructor(private router:Router, private restService:RestService, private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
console.log(this.index=this.activatedRoute.snapshot.params['id']);
    var body = {
      'userid': this.index
    }
  console.log(body);
    this.restService.postdetails(global.baseUrl+'viewsingleuser',body).subscribe((response:any)=>{
      if(response.message)
      {
        this.edit=response.message;;
       
       //console.log(this.edit);
      }
      else
      {
       console.log(response.message);
      }
    }); 
    var pribody = {
      'userid': this.index,
      'org_subs_id':1
    }
    //console.log(body);
    this.restService.postdetails(global.baseUrl+'viewuserpri',pribody).subscribe((response:any)=>{
      if(response.message)
      {
        this.pri=response.message;
    
       
       //console.log(this.pri);
      }
      else
      {
        console.log(response.message);
      }
    }); 
  }
  
}



