import { Component, OnInit,EventEmitter,Output } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import * as global from '../../../global';
import { Router } from '@angular/router';

import {RestService} from '../../RestService/restservice';

@Component({
  selector: 'app-create-user',
  templateUrl: './create-user.component.html',
  styleUrls: ['./create-user.component.css']
})
export class CreateUserComponent implements OnInit {
  userForm: FormGroup;
  expanded:boolean=false;
  rolesmeta:any;
  buttonWasClicked: boolean = false;
  userid="19";
  usertype="Employer";
  type="1";
  cmpny_id="13";
  viewusers:any;
  assignmeta:any;
  errorMessage:any;
  @Output() jobEmitter= new EventEmitter<number>();
  constructor(private router:Router, private restService:RestService ) { }

  ngOnInit() {
    this.restService.getdetails(global.baseUrl+'view_user').subscribe((response:any)=>{
      if(response.message)
      {
        this.viewusers=response.message;;
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 
    this.restService.getdetails(global.baseUrl+'rolesmeta').subscribe((response:any)=>{
      if(response.message)
      {
        this.rolesmeta=response.message;;
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 
    let assign:any;
    assign={
     userid:this.userid,
      user_type:this.type,
      cmpny_id:this.cmpny_id
      
    };
    this.restService.postdetails(global.baseUrl+'assignedmeta',assign).subscribe((response:any)=>{
      if(response.message)
      {
        this.assignmeta=response.message;;
       
   
      }
      else
      {
         //console.log(response.message);
      }
    }); 
    localStorage.setItem('userid', '19');
    localStorage.setItem('User_Type',"employer");
    this.userForm= new FormGroup( 
      {'email': new FormControl('',[Validators.required, Validators.email]),
      'username': new FormControl('',[Validators.required]),
      'userrole' : new FormControl('', [Validators.required]),
      'mobile': new FormControl('',[Validators.required]),
      'designation': new FormControl('',[Validators.required]),
      'assigned': new FormControl('',[Validators.required]),
      'password': new FormControl('',[Validators.required,Validators.pattern(global.passwordRegex)])});
  }
  setButtonClicked (clicked: boolean) {
    this.buttonWasClicked = clicked;
  }
setExpandable(clicked:boolean){
  this.expanded = clicked;
}

onCreate(){
    let body:any;
    body=
    {
      name:this.userForm.get('username').value,
      email:this.userForm.get('email').value,
      mobile_no:this.userForm.get('mobile').value,
      password:this.userForm.get('password').value,
      designation:this.userForm.get('designation').value,
      adminid:this.userid,
      cmpny_id:this.cmpny_id,
      usertype:this.usertype,
      assigned_to:this.userForm.get('assigned').value,
      userrole:this.userForm.get('userrole').value
    }
    this.restService.postdetails(global.baseUrl+'createuser',body).subscribe((response:any)=>
    {
      if(response.status){
      this.setButtonClicked(false);

  }
    else
    {
      //console.log(response);
      this.errorMessage=response.message;
      alert("please try again");
    }
    },(error : any)=>{
    
    });
    }




    viewuserdetails(ids:number)
    {
      //console.log(ids);
        this.jobEmitter.emit(ids);
        this.router.navigate(['viewuser/'+ids]);
    }
}






