import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { ChartModule } from 'angular2-highcharts';
import {CountDown} from "../../node_modules/angular2-simple-countdown/countdown";
import { DatePipe } from '@angular/common';
import { RestService } from './RestService/restservice';
import { DataTablesModule } from 'angular-datatables';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { DialogComponent } from './dialog/dialog.component';
import { AppMaterialModule } from './app-material/app-material.module';
import { UserSettingComponent } from './user-setting/user-setting.component';
import { SubscriptionComponent } from './subscription/subscription.component';
import { CreateUserComponent } from './user-setting/create-user/create-user.component';
import { UserprivilegesComponent } from './user-setting/userprivileges/userprivileges.component';
import { MyplanComponent } from './subscription/myplan/myplan.component';
import { BillingInfoComponent } from './subscription/billing-info/billing-info.component';
import { GeneratereportComponent } from './generatereport/generatereport.component';
import { JobsviewedComponent } from '../app/dashboard/jobsviewed/jobsviewed.component';
import { ViewuserComponent } from './user-setting/viewuser/viewuser.component';
import { JobseekerappliedComponent } from '../app/dashboard/jobseekerapplied/jobseekerapplied.component';
import { JobseekerrelevantComponent } from '../app/dashboard/jobseekerrelevant/jobseekerrelevant.component';
import { JobseekerirrelevantComponent } from '../app/dashboard/jobseekerirrelevant/jobseekerirrelevant.component';
import { JobsactiveComponent } from '../app/dashboard/jobsactive/jobsactive.component';
import { JobsinactiveComponent } from '../app/dashboard/jobsinactive/jobsinactive.component';
import { JobsclosedComponent } from '../app/dashboard/jobsclosed/jobsclosed.component';
import { JobstotalComponent } from '../app/dashboard/jobstotal/jobstotal.component';
import { EmployerviewedComponent } from '../app/dashboard/employerviewed/employerviewed.component';
import { EmployershortlistedComponent } from '../app/dashboard/employershortlisted/employershortlisted.component';
import { EmployerscheduledComponent } from '../app/dashboard/employerscheduled/employerscheduled.component';
import { EmployerselectedComponent } from '../app/dashboard/employerselected/employerselected.component';
import { EmployerrejectedComponent } from './dashboard/employerrejected/employerrejected.component';
import { ApprovedComponent } from './dashboard/approved/approved.component';
import { IncurationComponent } from './dashboard/incuration/incuration.component';
import { ExporttoexcelComponent } from  './exporttoexcel.component/exporttoexcel.component';
import { CollapsibleModule } from 'angular2-collapsible';
import { MatchedprofilesComponent } from './dashboard/matchedprofiles/matchedprofiles.component';
import { EdituserprevComponent } from './user-setting/userprivileges/edituserprev/edituserprev.component';

import { UserssettingsComponent, DialogOverviewExampleDialog } from './userssettings/userssettings.component';
import { EdituserdetailsComponent } from './userssettings/edituserdetails/edituserdetails.component';
import { ViewuserdetailsComponent } from './userssettings/viewuserdetails/viewuserdetails.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';

export const routes: Routes = [ 
  { path: '', component: HeaderComponent, children: [{path: '', component: DashboardComponent }, {path:'edituserdetails', component:EdituserdetailsComponent},{path:'edituserdetails/:id', component:EdituserdetailsComponent},
  {path:'viewuserdetails', component:ViewuserdetailsComponent},{path:'viewuserdetails/:id', component:ViewuserdetailsComponent},{ path: 'usersettings', component: UserssettingsComponent },{path: 'users', component: UserSettingComponent ,
  children: [{ path: '', component: CreateUserComponent },
  { path: 'userprivileges', component: UserprivilegesComponent } ] }, 
  {path: 'subscription', component: SubscriptionComponent ,
  children: [{ path: 'myplan', component: MyplanComponent },
  { path: 'billinginfo', component: BillingInfoComponent } ] }
  ,{ path: 'generate', component: GeneratereportComponent },
  { path: 'jviewed', component: JobsviewedComponent },
  { path: 'jviewed/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: JobsviewedComponent },
  { path: 'erejected', component: EmployerrejectedComponent },
  { path: 'erejected/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: EmployerrejectedComponent },
  { path: 'eselected', component: EmployerselectedComponent },
  { path: 'eselected/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: EmployerselectedComponent },
  { path: 'escheduled', component: EmployerscheduledComponent },
  { path: 'escheduled/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: EmployerscheduledComponent },
  { path: 'eshort', component: EmployershortlistedComponent },
  { path: 'eshort/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: EmployershortlistedComponent },
  { path: 'eviewed', component: EmployerviewedComponent },
  { path: 'eviewed/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: EmployerviewedComponent },
  { path: 'approved', component: ApprovedComponent },
  { path: 'approved/:flag/:by_location/:by_user/:from_date/:to_date', component: ApprovedComponent },
  { path: 'curation', component: IncurationComponent },
  { path: 'curation/:flag/:by_user/:by_location/:from_date/:to_date', component: IncurationComponent },
  { path: 'jtotal', component: JobstotalComponent },
  { path: 'jtotal/:flag/:by_user/:by_location/:from_date/:to_date', component: JobstotalComponent },
  { path: 'jactive', component: JobsactiveComponent },
  { path: 'jactive/:flag', component: JobsactiveComponent },
  { path: 'jinactive', component: JobsinactiveComponent },
  { path: 'jinactive/:flag', component: JobsinactiveComponent },
  { path: 'jclosed', component: JobsclosedComponent },
  { path: 'jclosed/:flag', component: JobsclosedComponent },
  { path: 'japplied', component: JobseekerappliedComponent },
  { path: 'japplied/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: JobseekerappliedComponent },
  { path: 'jrelevant', component: JobseekerrelevantComponent },
  { path: 'jrelevant/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: JobseekerrelevantComponent },
  { path: 'matched', component: MatchedprofilesComponent }, 
  { path: 'matched/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: MatchedprofilesComponent },
  { path: 'jirrelevant', component: JobseekerirrelevantComponent },
  { path: 'jirrelevant/:flag/:jobid/:by_location/:from_date/:to_date/:by_user', component: JobseekerirrelevantComponent },
  { path: 'viewuser', component: ViewuserComponent },
  {path:'viewuser/:id', component:ViewuserComponent},
  {path:'edituserprev', component:EdituserprevComponent},
  {path:'edituserprev/:id', component:EdituserprevComponent}
 ]},{path:'**', component:PagenotfoundComponent}
];

@NgModule({
  declarations: [
    ExporttoexcelComponent,
    AppComponent,
    HeaderComponent,
    CountDown,
    DashboardComponent,
    UserSettingComponent,
    SubscriptionComponent,
    CreateUserComponent,
    UserprivilegesComponent,
    MyplanComponent,
    BillingInfoComponent,
    CountDown,
    GeneratereportComponent,
    DialogComponent,
    JobsviewedComponent,
    ViewuserComponent,
    JobseekerappliedComponent,
    JobseekerrelevantComponent,
    JobseekerirrelevantComponent,
    JobsactiveComponent,
    JobsinactiveComponent,
    JobsclosedComponent,
    JobstotalComponent,
    EmployerviewedComponent,
    EmployershortlistedComponent,
    EmployerscheduledComponent,
    EmployerselectedComponent,
    EmployerrejectedComponent,
    ApprovedComponent,
    IncurationComponent,
    MatchedprofilesComponent,
    EdituserprevComponent,DialogOverviewExampleDialog, UserssettingsComponent, EdituserdetailsComponent, ViewuserdetailsComponent, PagenotfoundComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    DataTablesModule,
    HttpClientModule,
    CollapsibleModule,
    DataTablesModule,
    RouterModule.forRoot(routes),
    ReactiveFormsModule,
    BrowserAnimationsModule,
    AppMaterialModule,
  ],
  providers: [RestService],
  bootstrap: [AppComponent],
  entryComponents: [DialogOverviewExampleDialog]
})
export class AppModule { }



