import { Component, OnInit } from '@angular/core';
import { FormControl,  FormGroup, Validators } from '@angular/forms';
import { ExporttoexcelComponent } from  '../exporttoexcel.component/exporttoexcel.component';
@Component({
  selector: 'app-generatereport',
  templateUrl: './generatereport.component.html',
  styleUrls: ['./generatereport.component.css']
})
export class GeneratereportComponent implements OnInit {
  generateForm: FormGroup;
  constructor() { }

  ngOnInit() {
    this.generateForm= new FormGroup(
      { 'from' :new FormControl('',[Validators.required]),
    'to': new FormControl('',[Validators.required])
  });
}
  someData : any  = [
	  {name : "Ashish Santikari" , technol : "OO JavaScript"},
	  {name : "Ashish Santikari" , technol : "Angular 1"},
	  {name : "Ashish Santikari" , technol : "Angular 2"} 
  ];


}



