employeration(){
let emp:any;
emp={
  cmpny_id:this.cmpny_id,
  userid:this.userid,
  user_type:this.user_type,
  flag:this.flag,
  org_subs_id:1
};
this.restService.postdetails(global.baseUrl+ 'employeractions',emp).subscribe((response:any)=>{
  if(response.status)
  {
  
    this.employeractions=response.message;
   
    this.eviewed=this.employeractions[0].profiles_viewed;
    this.viewed=this.availableProfilesPercentage*this.eviewed;
    this.eshortlisted=this.employeractions[0].shortlisted;
    this.escheduled=this.employeractions[0].scheduled;
    this.eselected=this.employeractions[0].selected;
    this.erejected=this.employeractions[0].rejected;
    this.shortlisted=(this.eshortlisted/this.eviewed)*100;
    this.scheduled=(this.escheduled/this.eshortlisted)*100;
    this.selected=(this.eselected/this.escheduled)*100;
    this.rejected=(this.erejected/this.escheduled)*100;
  }
  else
  {
     console.log(response.message);
  }
});
}
jobsposted(){
let jobss:any;
jobss={
  cmpny_id:this.cmpny_id,
  userid:this.userid,
  user_type:this.user_type,
  flag:this.flag,
  org_subs_id:1
};
this.restService.postdetails(global.baseUrl+ 'jobs',jobss).subscribe((response:any)=>{
  if(response.status)
  {

    this.jobs=response.message;
    this.etjobs=this.jobs[0].total_jobs;
    this.eajobs=this.jobs[0].active_jobs;
    this.einajobs=this.jobs[0].inactive_jobs;
    this.ecjobs=this.jobs[0].closed_jobs;
    this.activejobs=(this.eajobs/this.etjobs)*100;
    this.inactivejobs=(this.einajobs/this.etjobs)*100;
    this.closedjobs=(this.ecjobs/this.etjobs)*100;
  }
  else
  {
     console.log(response.message);
  }
});
}
