import { Component, OnInit } from '@angular/core';
import * as global from '../../../global';
import { RestService } from '../../RestService/restservice';
@Component({
  selector: 'app-myplan',
  templateUrl: './myplan.component.html',
  styleUrls: ['./myplan.component.css']
})
export class MyplanComponent implements OnInit {
  edit:any;
  myfeatures:any;
  myplan:any
  myremaining:any;
  constructor(private restService:RestService) { }

  ngOnInit() {
    this.restService.getdetails(global.baseUrl+ 'subscriptionfeatures?cmpny_id=13').subscribe((response:any)=>{
      if(response.message)
      {
        this.myfeatures=response.message[0];
      
      }
      else
      {
         //console.log(response.message);
      }
    });
    this.restService.getdetails(global.baseUrl+ 'subscriptionplan?cmpny_id=13').subscribe((response:any)=>{
      if(response.message)
      {
        //console.log(response.message);
        this.myplan=response.message[0];
        //console.log(this.myplan);
      }
      else
      {
         //console.log(response.message);
      }
    });
    this.restService.getdetails(global.baseUrl+ 'subscriptionremaining?userid=19&cmpny_id=13').subscribe((response:any)=>{
      if(response.message)
      {
        //console.log(response.message);
        this.myremaining=response.message;
        
        //console.log(this.myremaining);

      }
      else
      {
         //console.log(response.message);
      }
    });
}
}



